@import "../../inc/vars";
.acrobits_next_post {
	display: grid;
	gap: 16px;
	align-items: center;
	padding: 40px 20px;
	text-align: center;
	@media (min-width: $screen-sm){
		text-align: left;
		padding: 40px 80px;
		grid-template-columns: 1fr 1fr;
		gap: 20px;
	}
	&__content {
		h2 {
			@include pop(400, 32, 1.24);
			margin-bottom: 20px;
			@media (min-width: $screen-sm){
				font-size: 29px;
			}
		}
		&__description {
			color: #4F4B58;
			@include fig(400, 20, 1.24);
		}
	}
	&__image {
		padding: 8px 8px 20px;
		box-shadow: 0px 2px 4px 0px rgba(7, 4, 146, 0.10), 0px 24px 60px 0px rgba(6, 47, 125, 0.05), 0px 12px 24px 0px rgba(27, 59, 119, 0.05);
		img {
			width: 100%;
		}
		a {
			border-bottom: none !important;
		}
		.acrobits_next_post__link {
			text-decoration: none;
			padding-left: 12px;
			padding-top: 12px;
			@include pop(400, 20, 1.36);
			color: #000;
			display: block;
			&:hover {
				color: $purple;
			}
		}
	}
}